import helloComp from "../../components/helloComp.vue";
import grade from "../../components/grade.vue";
export default {
	data() {
		return {
			title: 'Hello',
			animationData:{},
			animationDataarr:[
				{},{},{},{},{}
			],
			list:[],
			listc:[],
			listvideo:[
				"../../static/fulinas.Ogg",
				"../../static/jinqiduiz.Ogg",
				"../../static/heiguafu~1.mp4",
				"../../static/zhizux.Ogg"
			]
		}
	},
	onUnload(){
		this.animationData = {},
		this.animationDataarr = [
			{},{},{},{},{}
		]
	},
	onpullDownRefresh(){
		this.refresh();
	},
	onLoad() {
		// var serverUrl = common.serverUrl;
		//再页面创建的时候创建有个临时动画

			this.animation= uni.createAnimation();

		var serverUrl = this.serverUrl;
		//请求轮播图
		// uni.request({
		// 	url:'http://api.douban.com/v2/movie/nowplaying?apikey=0b2bdeda43b5688921839c8ecb20399b',
		// 	method:'POST',
		// 	success: (res) => {
		// 		console.log(res.data);
		// 		if(res.data.status == 200){
					// var a = res.data;
					// this.list = a;
		// 		}
		// 	},
		// 	error:(error) =>{
		// 		console.log(error);
		// 	}
		// }),
		//热门电影
		uni.request({
			url: 'http://t.yushu.im/v2/movie/in_theaters?city=南京&start=0&count=10',
			method:'GET',
			header: {
				'content-type': 'application/json'
			},
			success: (res) => {
				// if(this.status == 200){
					// console.log(res.data.subjects);
					var a = res.data.subjects;
					this.list = a.slice(0,6);
					
					// console.log();
				// }
			},
		}),
		//猜你喜欢
		uni.request({
			url: 'http://t.yushu.im/v2/movie/top250',
			method:'GET',
			header: {
				'content-type': 'application/json'
			},
			success: (res) => {
				// if(this.status == 200){
					console.log(res.data.subjects);
					var a = res.data.subjects;
					this.listc = a.slice(9,20);
					
					// console.log();
				// }
			}
		})
	},
	methods: {
		refresh(){
			uni.showLoading({
				mask:true
			});
			uni.showNavigationBarLoading();
			uni.request({
				url: 'http://t.yushu.im/v2/movie/top250',
				method:'GET',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					// if(this.status == 200){
						console.log(res.data.subjects);
						var a = res.data.subjects;
						this.listc = a.slice(0,9);
						
						// console.log();
					// }
				},
				complete:()=>{
					uni.hideNavigationBarLoading();
					uni.hideLoading();
				}
			})
			
		},
		praiseMe(e){
			var gIndex = e.currentTarget.dataset.gindex;
			console.log(gIndex);
			// return;
			//构建动画·1数据，并且通过step来表达着这组动画完成
			this.animation.translateY(-70).opacity(1).step({
				duration:400
			});
			//导出数据动画到view组件
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataarr[gIndex] = this.animationData.export();
			// 还原动画
			setTimeout(function(){
				this.animation.translateY(0).opacity(0).step({
					duration:0
				});
					// this.animationData = this.animation.export();
					this.animationData = this.animation;
					this.animationDataarr[gIndex] = this.animationData.export();
			}.bind(this),500)
		}
	},
	components:{   //注册自定组件
		helloComp,
		grade
	}
}
